.search-bar {
  margin: 20px;
  text-align: center;
}

.search-bar input {
  width: 75%;
  margin: 0 0 0 18px;
}

.search-bar .search-bar-title {
  font-size: 18px;
  text-transform: uppercase;
  letter-spacing: 1.4px;
  color: #777;
}

.video-item img {
  max-width: 64px;
}

.video-detail .details {
  margin-top: 10px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.list-group-item {
  cursor: pointer;
}

.list-group-item:hover {
  background-color: #eee;
}



